            <i class="fab fa-github fa-2x"></i>
            <i class="fab fa-qq fa-2x"></i>
            <i class="fab fa-weibo fa-2x"></i>
            <i class="fab fa-weixin fa-2x"></i>
        </div>
        <div class="links">
            <div v-for="item in items">
                <h1>{{item.title}}</h1>
                <ul>
                    <li v-for="link in item.links">
                        <i></i>
                        <a :href="link.href">{{link.title}}</a>
                    </li>
                </ul>
            </div>
        </div>
        
    </div>
</template>

<script>
    export default {
        data(){
            return {
                items: [
                    {
                        title: '项目',
                        links: [
                            {
                                title: '更新',
                                href: '#'
                            },
                            {
                                title: '获取文档',
                                href: '#'
                            },
                            {
                                title: '提交错误',
                                href: '#'
                            },
                            {
                                title: '帮助',
                                href: '#'
                            },
                        ]
                    },
                    {
                        title: '团体',
                        links: [
                            {
                                title: '提交问题',
                                href: '#'
                            },
                            {
                                title: '联系我们',
                                href: '#'
                            },
                            {
                                title: '@Kronos',
                                href: '#'
                            },
                        ]
                    },
                    {
                        title: '条约',
                        links: [
                            {
                                title: '提交问题',
                                href: '#'
                            },
                            {
                                title: '提交问题',
                                href: '#'
                            },
                            {
                                title: '提交问题',
                                href: '#'
                            },
                        ]
                    }
                ]
            }
        }
    }
</script>

<style scoped>
    #public-footer{
        background: #222;
        display: flex;
        justify-content: space-between;
        padding: 4rem 0rem;
    }
    .social{
        width: 50%;
        display: flex;
        justify-content: space-around;
    }
    .links{
        width: 50%;
        background: #111;
        padding: 2rem 0rem;
        display: flex;
        justify-content: space-around;
    }
    .links div h1{
        color: #FFF;
        font-size: 1.25rem;
        font-weight: normal;
        margin-bottom: 1rem; 
    }
    .links div li{
        margin: .25rem 0rem;
    }
    .links div a{
        color: #777;
    }
    i{
        color: #FFF;
    }
    a{
        color: #FFF;
    }
</style>





