<template>
  <div id="root">
    <PublicHeader/>
    <transition name="route" mode="out-in">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import PublicHeader from '@/components/public/PublicHeader'
export default {
  name: 'app',
  components: {
    PublicHeader
  },
  mounted(){
    this.change()
  },
  updated(){
    this.change()
  },
  methods:{
    change(){
      if(this.$route.path != '/')
      document.querySelector('.header-info').style.background = '#111'
      else
      document.querySelector('.header-info')
      .style.background = 'rgba(0,0,0,.5)'
    }
  }
}
</script>

<style>
::-webkit-scrollbar{
  display: none;
}
*{
  margin: 0px;
  padding: 0px;
  list-style: none;
  border: 0;
}
html, body{
  height: 100%;
  font-family: moti; 
}
a{
  text-decoration: none;
}
li{
  list-style: none;
}
#root {
  height: inherit;  
}
.header-info{
  background: #000;
}

.route-enter-active{
        opacity: 1;
        transition: all .5s ease;
    }
.route-leave-active{
        opacity: 0;
        transition: all  .5s ease;
    }
.route-enter{
        opacity: 0;
    }
.route-leave{
        opacity: 1;
    }
</style>



