<template>
  <div id="home">
      <!-- <PublicHeader/> -->
      <PublicBanner/>
      <div class="introduction">
          <h1>Kronos是什么？</h1>
          <div class="featrues">
              <div v-for="item in items" class="wow fadeIn" data-wow-delay=".5s">
                  <h1>{{item.title}}</h1>
                  <p>{{item.summary}}</p>
              </div>
          </div>
      </div>
      <PublicFooter/>
  </div>
</template>

<script>
    import PublicBanner from '@/components/public/PublicBanner'
    import PublicFooter from '@/components/public/PublicFooter'
    
    export default {
        name: 'home',
        components: {
            PublicBanner,
            PublicFooter
        },
        data() {
            return {
                items: [
                    {
                        title: "开源的",
                        summary: "Kronos时间管理器,遵循于GPL V2协定为互联网上的所有用户开放其源代码，Kronos希望有更多的开发者加入其中，使其变为最好用的时间管理器"
                    },
                    {
                        title: "强大的",
                        summary: "Kronos时间管理器,在依据简约设计理念的同时做到满足日常生活中的多种应用场景，做到简约但不简单"
                    },
                    {
                        title: "轻量的",
                        summary: "Kronos时间管理器，基于新一代WEB应用技术能够在任何支持Browser技术的设备上流畅运行"
                    },
                ]
            }
        }
    }
</script>

<style scoped>
    #home{
        background: rgba(0,0,0,.1);
        height: 100%;
    }
    #public-header >>> .header-info:hover{
        background: rgba(0, 0, 0, .5)
    }
    .introduction{
        position: relative;
    }
    .introduction > h1{
        margin: 2rem 0rem;
        text-align: center;
    }
    .featrues{
        width: 90%;
        margin: 4rem auto;
        display: flex;
        justify-content: space-around;
    }
    .featrues div{
        width: 20%;
        background-image: linear-gradient(#FA3, #C73);
        padding: 2rem;
    }
    .featrues div h1{
        color: #FFF;
        margin-bottom: 1rem; 
    }
    .featrues div p{
        color: #FFF;
        line-height: 1.5rem;
        text-align: justify;
    }
</style>





