<template>
    <header id="public-header">
        <div class="header-info">
            <div class="logo">Kronos</div>
            <nav class="header-navigation">
                <li v-for="navigation in navigations"><a :href="navigation.herf">{{navigation.name}}</a></li>
                <div class="user-center">
                    <div><a>登陆</a></div>
                    <div><a>注册</a></div>
                </div>
            </nav>
        </div>
    </header>
</template>

<script>
    import PublicNavigationBar from './PublicNavigationBar'
    import PublicBanner from './PublicBanner'

    export default {
        name: 'PublicHeader',
        data(){
            return{
                navigations: [{
                    name: '首页',
                    herf: '#/'
                },
                {
                    name: '产品',
                    herf: '#/introduction'
                },
                {
                    name: '博客',
                    herf: '#/blog'
                }]
            }
        },
        components: {
            PublicNavigationBar,
            PublicBanner
        }
    }
</script>

<style scoped>
    #public-header{
        width: 100%;
        display: flex;
        position: absolute;
        z-index: 1;
    }
    .header-info{
        width: 100%;
        display: flex;
        position: absolute;
        background: rgba(0,0,0,.5);
        transition: 1s
    }
    .header-navigation{
        width: 100%;
        display: flex;
    }
    li{
        font: 1rem '微软雅黑';
        padding: 0rem 1rem;
        margin: 0rem 1rem;
        display: flex;
        align-items: center;
    }
    a{
        color: #FFF;
        transition: .5s;
    }
    li:hover a{
        text-shadow: 0px 0px 2px #FFF;
        transition: .5s;
    }
    .user-center{
        height: 100%;
        display: flex;
        position: absolute;
        right: 0;
        align-items: center;
    }
    .user-center div{
        padding: .5rem 1rem;
        margin: 0rem 1rem;
    }
    .user-center div a{
        color: #FA0
    }
    .logo{
        color: #FFF;
        font-size: 2rem;
        font-weight: bolder;
        padding: 1rem 3rem;
    }
</style>





