<template>
    <div id="public-banner">
        <transition-group tag="ul" class="slide-ul" name="list" mode="out-in">
            <li 
                v-for="(item, index) in items"
                v-show="index===currentIndex"
                :key="index"
                >
                <transition name="infor">
                    <div class="banner-info">
                    <h1>{{item.title}}</h1>
                    <p>{{item.summary}}</p>
                    <div><a :href="item.href">{{item.buttom}}</a></div>
                    
                    </div>
                </transition>
                <img :src="item.imgsrc">
            </li>
        </transition-group>
        <div class="banner-bar">
            <li
            class="banner-bar-li"
            v-for="(item, index) in items"
            :class="{'active':index===currentIndex}"></li>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return {
                items: [
                    {
                        title: 'Kronos时间管理器',
                        summary: '从现在开始获取Kronos时间管理器开始记录任何你想记录的事情',
                        buttom: '立即下载',
                        href: "static/Kronos-v0.0.1-installer.exe",
                        imgsrc: 'static/pic2.jpg',
                    },
                    {
                        title: '想让Kronos变得更好？',
                        summary: 'Kronos是一款开源的移动应用程序，如果你想让它变得更好你可以加入Github社区提出意见或是贡献代码',
                        buttom: '前往项目主页',
                        href: "https://github.com/MrChinico",
                        imgsrc: 'static/pic.jpg'
                    },
                    {
                        title: '实例标题',
                        summary: '实例说明：这是一段实例说明你可以在这里填写你想填写的项目说明',
                        buttom: '实例按钮',
                        imgsrc: 'static/pic3.jpg'
                    }
                ],
                currentIndex: 0
            }
        },
        methods: {
            autoPlay(){
                this.currentIndex++
                if(this.currentIndex > this.items.length - 1)
                    this.currentIndex = 0
            }
        },
        created(){
            this.$nextTick(() => {
                this.timer = setInterval(() => {
                    this.autoPlay()
                }, 7000)
            })
        },
        
    }
</script>

<style scoped>
    #public-banner{
        width : 100%;
        height: 100%;
        background-size: cover;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .slide-ul{
        width: 100%;
        height: 100%;
        background: rgba(255,0,0,.25);
        display: flex;
        align-items: center;
    }
    .slide-ul li{
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        position: absolute;
    }
    .slide-ul img{
        width: 100%;
        height: 100%;
    }
    .banner-info{
        position: absolute;
        display: flex;
        flex-flow: column;
    }
    .banner-info h1::first-letter{
        color: #FA0;
    }
    .banner-info h1{
        width: 30rem;
        color: #FFF;
        margin: 1rem 0rem;
        padding-left: 6rem;
    }
    .banner-info p{
        width: 30rem;
        height: 3rem;
        color: #FFF;
        padding-left: 8rem;
    }
    .banner-info div{
        width: 50%;
        background: #FA0;
        color: #FFF;
        margin-top: 3rem;
        padding: 1rem 2rem;
        transition: .5s;
        display: inline;
        text-align: right;
    }
    .banner-info div:hover{
        background: #D80;
        transition: .5s;
    }
    .banner-info div a{
        color: #FFF;
    }
    .banner-bar{
        background: rgba(255,255,255,.5);
        right: 2rem;
        padding: .5rem 0rem;
        position: absolute;
        border-radius: 1rem;
    }
    .banner-bar li{
        width: .5rem;
        height: .5rem;
        border-radius: .25rem;   
        margin: 1rem .25rem;
    }
    .banner-bar-li{
        background: rgba(0, 0, 0, .5);
        transition: .5s;
    }
    
    .list-enter-active{
        opacity: 1;
        transition: all 1s ease;
    }
    .list-leave-active{
        opacity: 0;
        transition: all  1s ease;
    }
    .list-enter{
        opacity: 0;
    }
    .list-leave{
        opacity: 1;
    }
    .infor-enter-active{
        opacity: 1;
        transform: translateX(0rem);
        transition: all 1s ease;
    }
    .infor-leave-active{
        opacity: 0;
        transform: translateX(1rem);
        transition: all 1s ease;
    }
    .infor-enter{
        opacity: 0;
        transform: translateX(1rem);
    }
    .infor-leave{
        opacity: 1;
        transform: translateX(0rem);
    }
    .active{
        background: #FA0;
        box-shadow: 0px 0px 10px #FA0;
        transition:  .5s
    }
</style>





