<template>
    <div id="blog">
        <div class="main">
            <transition-group tag="ul" class="index">
                <li
                    v-for="(item, index) in items"
                    :key="index">
                    <h1>{{item.title}}</h1>
                    <div class="sort">
                        <span v-for="sort in item.sorts">{{sort.text}}</span>
                    </div>
                    <p>{{item.summary}}</p>
                </li>
            </transition-group>
            <div class="archive">
                归档
            </div>
        </div>
        <PublicFooter/>
    </div>
</template>

<script>
    import PublicFooter from '@/components/public/PublicFooter'
    export default {
        components: {
            PublicFooter
        },
        data() {
            return {
                items: [
                    {
                        title: "Kronos源码现已公布",
                        sorts: [
                            {
                                text: '公告'
                            }
                        ],
                        summary: '这是一段示例博客内容页的摘要内容，若想查看详细内容请点击阅读全文按钮'
                    },
                    {
                        title: "12月开发进程",
                        sorts: [
                            {
                                text: '公告'
                            }
                        ],
                        summary: '这是一段示例博客内容页的摘要内容，若想查看详细内容请点击阅读全文按钮'
                    },{
                        title: "关于Kronos第一版功能的征集活动",
                        sorts: [
                            {
                                text: '公告'
                            }
                        ],
                        summary: '这是一段示例博客内容页的摘要内容，若想查看详细内容请点击阅读全文按钮'
                    },
                    {
                        title: "Kronos立项成功",
                        sorts: [
                            {
                                text: '公告'
                            }
                        ],
                        summary: '这是一段示例博客内容页的摘要内容，若想查看详细内容请点击阅读全文按钮'
                    },

                ]
            }
        }
    }
</script>

<style scoped>
    #blog{
        background: #EEE;
        padding-top: 6rem;
    }
    .main{
        display: flex;
    }
    .index{
        margin: 0rem 8rem; 
        padding: 0rem 2rem;
        background: #FFF;
    }
    .index li{
        border-bottom: 1px solid #CCC;
        padding: 2rem 0rem;
    }
    .archive{
        background: #FFF;
    }
    .sort span{
        background: #222;
        padding: .25rem 1rem;
        margin: 1rem 0rem;
        display: inline-block;
        font-size: .8rem;
        color: #FFF;
    }
</style>





